import React, { useRef, useState } from 'react';
import { AppBar, Toolbar, Typography, IconButton, Box, Button, Menu, MenuItem } from '@mui/material';
import { Menu as MenuIcon, FileUpload as FileUploadIcon, FileDownload as FileDownloadIcon } from '@mui/icons-material';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../store';
import { Transaction } from '../types/transaction';
import { addTransaction } from '../store/transactionSlice';
import logo from '../assets/logo.png';

interface HeaderProps {
    onMenuClick: () => void;
}

const Header: React.FC<HeaderProps> = ({ onMenuClick }) => {
    const fileInputRef = useRef<HTMLInputElement>(null);
    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
    const dispatch = useDispatch();
    const transactions = useSelector((state: RootState) => state.transactions.transactions);

    const handleExport = () => {
        // CSV
        const headers = ['', '', '', '', '', ''];
        const csvContent = [
            headers.join(','),
            ...transactions.map(t => [
                t.date,
                t.type,
                t.amount,
                // 0
                t.type === 'income' ? (t.cost !== undefined ? t.cost : '') : '',
                t.category,
                `"${t.description.replace(/"/g, '""')}"`  // 
            ].join(','))
        ].join('\n');

        //  BOM  Excel  UTF-8 
        const BOM = '\ufeff';
        // 
        const blob = new Blob([BOM + csvContent], { type: 'text/csv;charset=utf-8;' });
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = `fintrack_backup_${new Date().toISOString().split('T')[0]}.csv`;
        link.click();
    };

    const handleImport = (event: React.ChangeEvent<HTMLInputElement>) => {
        const file = event.target.files?.[0];
        if (!file) return;

        const reader = new FileReader();
        reader.onload = (e) => {
            const text = e.target?.result as string;
            // 
            const lines = text.replace(/\r\n/g, '\n').split('\n');

            // CSV
            const headerLine = lines[0];
            // CSV
            const parseCSVLine = (line: string): string[] => {
                const values: string[] = [];
                let current = '';
                let inQuotes = false;
                
                for (let i = 0; i < line.length; i++) {
                    const char = line[i];
                    if (char === '"') {
                        if (inQuotes && line[i + 1] === '"') {
                            current += '"';
                            i++;
                        } else {
                            inQuotes = !inQuotes;
                        }
                    } else if (char === ',' && !inQuotes) {
                        values.push(current);
                        current = '';
                    } else {
                        current += char;
                    }
                }
                values.push(current);
                return values;
            };
            
            const headers = parseCSVLine(headerLine);
            const hasCostColumn = headers.includes('') || headers.length > 5;
            const costIndex = headers.includes('') ? headers.indexOf('') : 3; // 3
            
            // 
            lines.slice(1).forEach(line => {
                if (!line.trim()) return; // 
                
                // CSV
                const values = parseCSVLine(line);
                
                // 
                const dateIndex = 0;
                const typeIndex = 1;
                const amountIndex = 2;
                const categoryIndex = hasCostColumn ? 4 : 3;
                const descriptionIndex = hasCostColumn ? 5 : 4;
                
                const transactionType = values[typeIndex] as 'income' | 'expense';
                const transaction: Transaction = {
                    id: crypto.randomUUID(),
                    date: values[dateIndex],
                    type: transactionType,
                    amount: parseFloat(values[amountIndex]) || 0,
                    category: values[categoryIndex] || '',
                    description: values[descriptionIndex]?.replace(/^"|"$/g, '').replace(/""/g, '"') || '',
                };

                // 
                if (transactionType === 'income' && hasCostColumn && values[costIndex] !== undefined && values[costIndex].trim() !== '') {
                    const costValue = parseFloat(values[costIndex]);
                    if (!isNaN(costValue)) {
                        transaction.cost = costValue;
                    }
                }

                //  store
                dispatch(addTransaction(transaction));
            });
        };
        reader.readAsText(file);
        
        // 
        if (fileInputRef.current) {
            fileInputRef.current.value = '';
        }
    };

    return (
        <AppBar 
            position="static" 
            sx={{ 
                background: 'linear-gradient(45deg, #FFD700 30%, #DAA520 90%)',
                boxShadow: '0 3px 5px 2px rgba(218, 165, 32, .3)'
            }}>
            <Toolbar>
                <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
                    <Box
                        component="img"
                        src={logo}
                        alt="FinTrack Logo"
                        sx={{
                            height: 40,
                            mr: 2,
                            objectFit: 'contain'
                        }}
                    />
                    <Typography variant="h6" component="div">
                        FinTrack - 
                    </Typography>
                </Box>
                <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                    {/* Desktop / larger screens: show buttons */}
                    <Box sx={{ display: { xs: 'none', sm: 'flex' }, gap: 1 }}>
                        <Button
                            color="inherit"
                            startIcon={<FileUploadIcon />}
                            onClick={() => fileInputRef.current?.click()}
                        >
                            
                        </Button>
                        <Button
                            color="inherit"
                            startIcon={<FileDownloadIcon />}
                            onClick={handleExport}
                        >
                            
                        </Button>
                    </Box>

                    {/* Mobile: menu icon that opens a menu with import/export */}
                    <Box sx={{ display: { xs: 'flex', sm: 'none' } }}>
                        <IconButton
                            color="inherit"
                            aria-label="open import export menu"
                            onClick={(e) => setAnchorEl(e.currentTarget)}
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            anchorEl={anchorEl}
                            open={Boolean(anchorEl)}
                            onClose={() => setAnchorEl(null)}
                        >
                            <MenuItem onClick={() => { setAnchorEl(null); fileInputRef.current?.click(); }}>
                                <FileUploadIcon sx={{ mr: 1 }} /> 
                            </MenuItem>
                            <MenuItem onClick={() => { setAnchorEl(null); handleExport(); }}>
                                <FileDownloadIcon sx={{ mr: 1 }} /> 
                            </MenuItem>
                        </Menu>
                    </Box>

                    <input
                        type="file"
                        ref={fileInputRef}
                        accept=".csv"
                        style={{ display: 'none' }}
                        onChange={handleImport}
                    />
                </Box>
            </Toolbar>
        </AppBar>
    );
};

export default Header;